<template>
  <div id="app">
    <h1 v-text="title"></h1>
    <input v-model="newItem" v-on:keyup.enter="addNew">
    <ul>
      <li v-for="item in items" v-bind:class="{finished: item.isFin}" v-on:click="del(item)">
        {{item.label}}
      </li>
    </ul>
  </div>
</template>

<script>
import Store from './store.js'
export default {
  data: function () {
    return {
      title: 'To do list',
      items: Store.fetch(),
      newItem: ''
    }
  },
  watch: {
    items: {
      handler: function (items) {
        Store.save(items)
      },
      deep: true
    }
  },
  methods: {
    toggleFinish: function (item) {
      item.isFin = !item.isFin
    },
    addNew: function () {
      this.items.push({
        label: this.newItem,
        isFin: false
      })
      this.newItem = ''
    },
    del: function (item) {
      var index = this.items.indexOf(item)
      this.items.splice(index, 1)
    }

  }
}
</script>

<style>
html{
  height: 100%;
}

body{
  display: flex;
  justify-content: center;
  height: 100%;
}

ul li{
  cursor: pointer;
}

ul li button{
  float: right;
  margin-left: 30px;
  display: none;
}

ul .finished:hover button{
  display: inline-block;
}

.finished{
  text-decoration: underline;
}
</style>



